const databaseModalMixin = {
  props: {
    visible: {
      type: Boolean,
      required: true,
    },
    dbType: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      originData: {}, // 
      // triggerchange modal
      // change
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { max: 20, message: '20', trigger: 'blur' },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
        hostIp: [
          { required: true, message: 'IP', trigger: 'blur' },
          {
            pattern:
              '^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?).){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$',
            message: 'IP',
            trigger: 'blur',
          },
        ],
        instanceName: [
          {
            required: true,
            message: `${
              this.dbType === 'sqlserver' ? '' : ''
            }`,
            trigger: 'blur',
          },
          {
            max: 20,
            message: '20',
            trigger: 'blur',
          },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { length: 20, message: '20', trigger: 'blur' },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
      },
      collapseName: '',
      confirmBtnLoading: false,
    };
  },
  computed: {
    _visible: {
      get() {
        return this.visible;
      },
      set(value) {
        if (!value) {
          this.$emit('update:visible', value);
        }
      },
    },
    // 
    databaseOrInstance() {
      return this.dbType === 'sqlserver' ? '' : '';
    },
  },
  methods: {
    // 
    cancel() {
      this.hasModifiedBeforeClose(() => {
        this.$emit('update:visible', false); // modal
      });
    },
    // 
    beforeClose(done) {
      this.hasModifiedBeforeClose(done);
    },
  },
};

const fileHostModalMixin = {
  props: {
    visible: {
      type: Boolean,
      required: true,
    },
    hostType: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      // triggerchange modal
      // change
      rules: {
        hostName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 20,
            message: '320',
            trigger: 'blur',
          },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
        hostIp: [
          { required: true, message: 'IP', trigger: 'blur' },
          {
            pattern:
              '^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?).){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$',
            message: 'IP',
            trigger: 'blur',
          },
        ],
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { length: 20, message: '20', trigger: 'blur' },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: '^[^\\s]*$',
            message: '',
            trigger: ['blur'],
          },
        ],
      },
    };
  },
  computed: {
    _visible: {
      get() {
        return this.visible;
      },
      set(value) {
        if (!value) {
          this.$emit('update:visible', value);
        }
      },
    },
  },
  methods: {
    // 
    cancel() {
      this.hasModifiedBeforeClose(() => {
        this.$emit('update:visible', false); // modal
      });
    },
    // 
    beforeClose(done) {
      this.hasModifiedBeforeClose(done);
    },
  },
};

export { databaseModalMixin, fileHostModalMixin };



