<template>
  <div id="app">
    <el-row type="flex" class="row-bg" justify="end">
      <el-col :span="19"></el-col>
      <el-col :span="5">
        <!--  -->
        <el-button type="primary" @click="handleCreate()"></el-button>
        <el-button type="primary" icon="el-icon-delete" :disabled="delDisabled" @click="delAll()">
        </el-button>
      </el-col>
    </el-row>
    <el-row :gutter="20">
      <el-col :span="24">
        <el-table v-loading="loading" element-loading-text="..." :data="tableUsers" stripe style="width: 100%" @selection-change="handleSelectionChange">

          <el-table-column type="selection">
          </el-table-column>
          <el-table-column prop="loginName" label="">
          </el-table-column>
          <el-table-column prop="userName" label="">
          </el-table-column>
          <el-table-column prop="state" label="" :filters="[{ text: '', value: 1 }, { text: '', value: 0 }]" :filter-method="filterState" filter-placement="bottom-end">
            <template slot-scope="scope">
              <span v-if="scope.row.state === 1">
                <!-- <el-tooltip content="Bottom center" placement="bottom" effect="light"> -->
                <i class="el-icon-circle-check" style="color: #67C23A;font-size: 18px">
                </i>
                <!-- </el-tooltip> -->
              </span>
              <span v-else>
                <i class="el-icon-remove" style="color: #909399;font-size: 18px"></i>
              </span>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <span v-for="item in scope.row.roles" :key="item.id" :label="item.name" :value="item.id">&nbsp;&nbsp;{{ item.name }}&nbsp;&nbsp;</span>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="130" header-align="center" align="center">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>

    <!--  begin-->
    <el-dialog title="" :visible.sync="dialogUpdateVisible" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form id="#update" :model="update" ref="update" label-width="100px">
        <el-form-item label="" prop="loginName">
          <el-input v-model="update.loginName" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="update.userName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="12">
            <el-form-item>
              <el-select v-model="rolesSelected" multiple collapse-tags placeholder="">
                <el-option v-for="item in rolesAll" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item>
              <el-radio-group v-model="update.state">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-tooltip content="111111" placement="right">
            <el-button @click="handlereset(update.id)" :disabled="passDisable"></el-button>
          </el-tooltip>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogUpdateVisible = false"> </el-button>
        <el-button type="primary" :loading="updateLoading" @click="updateUser"> </el-button>
      </div>
    </el-dialog>
    <!--  end-->
    <!--  begin-->
    <el-dialog title="" :visible.sync="dialogCreateVisible" :close-on-click-modal="false" :close-on-press-escape="false">
      <el-form id="#create" :model="create" status-icon :rules="rules" ref="create" label-width="100px">
        <el-form-item label="" prop="loginName">
          <el-input v-model="create.loginName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="userName">
          <el-input v-model="create.userName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-col :span="12">
            <el-form-item>
              <el-select v-model="rolesSelected" multiple collapse-tags placeholder="">
                <el-option v-for="item in rolesAll" :key="item.id" :label="item.name" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item>
              <el-radio-group v-model="create.state">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="create.password" type="password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="checkpass">
          <el-input v-model="create.checkpass" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogCreateVisible = false"> </el-button>
        <el-button type="primary" :loading="createLoading" @click="createUser"> </el-button>
      </div>
    </el-dialog>
    <!--  end-->
  </div>
</template>

<script>
import {
  createUserInfo,
  getUsersInfo,
  updateUserInfo,
  deleteUserInfo,
  deleteUsersInfo,
} from '../../api/user';

const rolesUser = [
  {
    id: 'admin',
    name: '',
  },
  {
    id: 'file admin',
    name: '',
  },
  {
    id: 'oracle dba',
    name: 'Oracle',
  },
  {
    id: 'sql server dba',
    name: 'SQL Server',
  },
  {
    id: 'vm admin',
    name: '',
  },
];

export default {
  created() {
    this.getUsers();
  },
  data() {
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else if (value !== this.create.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    const validateName = (rule, value, callback) => {
      const data = this.tableUsers;
      let j = 0;
      for (let i = 0; i < data.length; i++) {
        if (value === data[i].loginName) {
          callback(new Error('!'));
          j = 1;
        }
      }
      if (j === 0) {
        callback();
      }
    };

    return {
      loading: true,
      delDisabled: false,
      passDisable: false,
      createLoading: false,
      updateLoading: false,
      dialogCreateVisible: false, // 
      dialogUpdateVisible: false, // 
      listIndex: '', // s
      rules: {
        loginName: [
          { required: true, message: '', trigger: 'blur' },
          { max: 15, message: ' 15 ' },
          { pattern: /^[A-Za-z0-9]+$/, message: '' },
          { validator: validateName },
        ],
        userName: [
          { required: false, message: '', trigger: 'blur' },
          { min: 3, max: 25, message: ' 3  25 ' },
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 25, message: ' 6  25 ' },
        ],
        checkpass: [
          { required: true, validator: validatePass, trigger: 'blur' },
        ],
      },
      rolesAll: rolesUser,
      rolesSelected: [],
      tableUsers: [],
      multipleSelection: [], // 
      update: {
        id: '',
        loginName: '',
        userName: '',
        state: '',
        roles: [],
      },
      create: {
        loginName: '',
        userName: '',
        state: 1,
        password: '',
        checkpass: '',
        roles: [],
      },
    };
  },
  methods: {
    // 
    filterState(value, row) {
      return row.state === value;
    },
    // 
    handleCreate() {
      this.rolesSelected = [];
      this.dialogCreateVisible = true;
      this.$nextTick(() => {
        // dom
        this.$refs.create.resetFields();
      });
    },
    // 
    handleEdit(index, row) {
      const newRow = Object.assign({}, row);
      this.update = newRow;
      this.dialogUpdateVisible = true;
      this.passDisable = false;
      this.rolesSelected = newRow.roles.map(item => item.id);
      // 
      this.listIndex = index;
    },
    // 
    handlereset(value) {
      this.$confirm(
        ` ${this.update.loginName} `,
        '',
        { type: 'warning' }
      )
        .then(() => {
          // 
          const resetUpdate = {
            id: value,
            password: '111111',
          };
          updateUserInfo(resetUpdate)
            .then(response => {
              this.$message.success(response.data.message);
              this.passDisable = true;
            })
            .catch(error => {
              this.$message.error(error);
            });
        })
        .catch(() => {
          this.$message.info('!');
        });
    },
    // 
    handleDelete(index, row) {
      this.$confirm(
        ` ${row.loginName} `,
        '',
        { type: 'warning' }
      )
        .then(() => {
          // 
          deleteUserInfo(row.id)
            .then(response => {
              this.$message.success(
                ` ${row.loginName}!${response.data.message}`
              );
              this.tableUsers.splice(index, 1);
              // this.getUsers();
            })
            .catch(error => {
              this.$message.error(error);
            });
        })
        .catch(() => {
          this.$message.info('!');
        });
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    getUsers() {
      this.loading = true;
      getUsersInfo()
        .then(response => {
          this.tableUsers = response.data.data;
          // this.pagination.tableRows = this.tableUsers.length;
          this.loading = false;
        })
        .catch(error => {
          this.loading = false;
          this.$message.error(error);
        });
    },
    // 
    createUser() {
      this.$refs.create.validate(valid => {
        if (valid) {
          this.createLoading = true;
          createUserInfo(this.create)
            .then(response => {
              this.$message.success(response.data.message);
              this.dialogCreateVisible = false;
              this.createLoading = false;
              this.tableUsers.push(response.data.data);
            })
            .catch(error => {
              this.createLoading = false;
              this.$message.error(error);
            });
        } else {
          return false;
        }
      });
    },
    // 
    updateUser() {
      this.updateLoading = true;
      updateUserInfo(this.update)
        .then(response => {
          this.$message.success(response.data.message);
          this.dialogUpdateVisible = false;
          this.updateLoading = false;
          // list
          this.tableUsers.splice(this.listIndex, 1, response.data.data);
        })
        .catch(error => {
          this.updateLoading = false;
          this.$message.error(error);
        });
    },
    // 
    delAll() {
      this.$confirm(
        ` ${
          this.multipleSelection.length
        } , ?`,
        '',
        {
          type: 'warning',
        }
      )
        .then(() => {
          const ids = [];
          this.multipleSelection.map(item => {
            ids.push(item.id);
          });
          const data = { ids };
          // 
          deleteUsersInfo(data)
            .then(response => {
              this.$message.success(
                ` ${this.multipleSelection.length} ! ${
                  response.data.message
                }`
              );
              this.getUsers();
            })
            .catch(error => {
              this.$message.error(error);
            });
        })
        .catch(() => {
          this.$message.info('!');
        });
    },
    // Roles
    transformRoles(newVal) {
      /* this.create.roles = newVal.map(i=> {
        for(let j=0; j< rolesUser.length; j++){
          if(rolesUser[j].id === i){
             return rolesUser[j]
          }
        }
      }); */
      this.create.roles = newVal;
      this.update.roles = newVal;
    },
  },
  watch: {
    rolesSelected: function(newVal) {
      this.transformRoles(newVal);
    },
  },
};
</script>



